import * as React from "react"
import Layout from "../components/layout"
import * as styles from '../components/pages/media.css'
import { graphql } from "gatsby"


// https://my.polyv.net/secure/main
export default function Media(props) {
  const vids = ['c462584f45dc0fc38f61f164be745e8d_c','c462584f45408232eed34d7316dbcaa8_c','c462584f45b12caf1bec080a3c7c12cd_c']
  return (
    <Layout >
            {vids.map((vid)=> {
    return (
      <div className={styles.Wrapper} >
            <iframe 
                title="video"
                key={vid}
                className={styles.IframeStyle}
                frameborder='no' 
                allowfullscreen 
                mozallowfullscreen 
                webkitallowfullscreen 
                allow="autoplay"
                src={`https://share.plvideo.cn/front/video/preview?vid=${vid}`}>
            </iframe>
        </div>
         )}
         )}
    </Layout>
  )
}


export const query = graphql`
  {
    aboutPage {
      id
      title
      description
      image {
        id
        url
      }
      blocks: content {
        id
        blocktype
        ...AboutHeroContent
      }
    }
  }
`

